@import url("https://fonts.googleapis.com/css2?family=Pacifico&family=VT323&display=swap");
@import url("https://fonts.googleapis.com/css?family=ABeeZee&family=ABeeZee&display=swap");

#app-main {
  background-color: #f8fbfc;
}

body {
  font-family: ABeeZee;
}

.logo {
  text-align: left;
  font-size: 2vw;
  font-weight: bolder;
  padding: 1vw;
  padding-left: 1vw;
  border: 0;
}

.icon {
  height: 2vw;
  width: 2vw;
  padding-right: 1vw;
}

.icon#home {
  padding-right: 1vb;
}

.plant-image {
  display: flex;
  align-items: center;
  width: 100px;
  height: 100px;
}

.plant-image:hover {
  cursor: pointer;
  transition: 300ms ease;
  width: 120px;
  height: 120px;
}

/* App.css */

/* Modal styles */
.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #f8fbfc;
  padding: 20px;
  border-radius: 5px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.intro-section {
  text-align: center;
  padding: 20px;
  font-family: "VT323";
}

.image-section {
  padding-bottom: 4vh;
  height: 96vh;
  min-height: fit-content;
  position: relative;
  text-align: center;
}

.frontimage {
  width: 100%;
  height: 50vh;
  max-width: 1;
  object-fit: cover;
  padding-top: 0;
}

@keyframes slideInFromTop {
  0% {
    background-color: #fefefe00;
    transform: translate(-50%, -300%);
  }
  100% {
    background-color: #fefefe98;
    transform: translate(-50%, -80%);
  }
}

.image-text {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 37%;
  left: 50%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: xx-large;
  height: 40%;
  width: 100%;
  animation-fill-mode: both;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-name: slideInFromTop;
}

.react-typewriter-text,
.react-typewriter-pointer {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.cta-btn {
  background-color: #64d345ac; /* Green */
  color: white;
  padding: 15px 30px; /* Increased padding for a bigger button */
  border: none;
  border-radius: 25px; /* Increased border-radius for a rounded button */
  cursor: pointer;
  font-size: 18px; /* Increased font size */
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease;
  margin-top: 6rem;
  display: inline-block; /* Added to ensure the button width adjusts according to content */
  position: relative; /* Added for pseudo-element */
  overflow: hidden; /* Added for pseudo-element */
}

.cta-btn:hover {
  background-color: #45a049; /* Darker green */
  transform: translateY(-2px); /* Move button slightly upwards */
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2); /* Increased shadow effect */
}

#welcome {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#sprout-view {
  margin-left: 1vw;
}

.choice {
  color: black;
  font-size: large;
}

.flex {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center; /* Align items vertically */
}

.plant-section {
  margin-top: 4vh;
  margin: 0 10rem;
  box-sizing: border-box;
  min-height: fit-content;
}

.icon {
  margin-top: 1 px; /* Adjust the margin-top value as needed */
}

.choice {
  margin-left: 10px; /* Add margin between the icon and text */
  font-size: 30px;
}

.image-text .intro-section h2 {
  font-size: 50px; /* Adjust the font size as needed */
}
