@import url("https://fonts.googleapis.com/css2?family=Pacifico&family=VT323&display=swap");
@import url("https://fonts.googleapis.com/css?family=ABeeZee&family=ABeeZee&display=swap");

body {
  background-color: #e8f9df;
}

.options-section {
  width: 20vw;
  max-width: max-content;
}

.options-section h2 {
  padding-bottom: 10px;
}

.options-container {
  margin: 0 auto;
}

.season-options {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.options-section button,
.season-options button {
  background-color: #bfdb9d;
  font-family: ABeeZee;
  font-size: 16px;
  padding: 10px 24px;
  text-align: center;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.options-section button:hover {
  background-color: rgb(138, 165, 118);
}

.options-section button:focus {
  background-color: rgb(138, 165, 118);
}

.country {
  font-family: ABeeZee;
}

.Summer {
  margin-inline: 10px;
}

.Autumn {
  margin-inline: 10px;
}

.Winter {
  margin-inline: 10px;
}

.Late {
  margin-inline: 10px;
}

.Full-sun {
  margin-inline: 10px;
}

.Partial-sun {
  margin-inline: 10px;
}

.Shade {
  margin-inline: 10px;
}

.font-container {
  text-align: center;
  font-family: VT323;
  font-size: 20px;
}

.topic {
  padding-top: 40px;
  margin-bottom: 70px;
  color: rgb(40, 40, 40);
}

.container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.options-section {
  margin-bottom: 30px;
  padding: 20px;
  border-radius: 10px;
  margin-left: 40px;
}

.options-section h2 {
  margin-bottom: 10px;
  font-family: ABeeZee;
  color: #0c0c0c;
}

.radio-container {
  display: inline-block;
  flex-direction: column;
}

.light-options {
  color: #4caf50;
}

.options-section .slidecontainer {
  margin-top: 10px;
}

.options-section .slider {
  width: 60%;

  appearance: none;
  height: 5px;
  border-radius: 5px;
  background-color: #ccc;
}

.options-section .slider::-webkit-slider-thumb {
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgb(127, 156, 104);
  cursor: pointer;
}
.options-section .slider-value {
  margin-top: 5px;
  color: #799655;
}

.place {
  font-size: 16px;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 200px;
  background-color: rgb(130, 171, 109);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  font-family: "Lucida Consode", monospace;

  bottom: -10%;
  left: 100%;
  margin-left: -5px;

  opacity: 0;
  transition: opacity 0.75s;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.confirm-zone {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.climate-zone h2 {
  margin-bottom: 10px;
  color: #4caf50;
}

.zone {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
}

.button {
  text-align: center;
  padding: 10px 20px;
  background-color: #4caf50;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #45a049;
}

.secondary-buttons {
  width: fit-content; /* Ensures the button width is as wide as its content */
  padding-top: 10px;
}

.confirm-button {
  display: block; /* Makes the button a block element */
  width: fit-content; /* Ensures the button width is as wide as its content */
  margin: 10px; /* Centers the button horizontally and adds space above */
  margin-left: 160px;
}

.potSize-dropdown {
  width: 40%; /* Makes the dropdown full width of its container */
  padding: 8px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: white;
  margin: 10px 0;
}

.square {
  padding-top: 70px;
  padding-bottom: 70px;
  max-height: max-content;
  background-color: #fdfdfd;
  justify-content: space-between;
}

.options-container .image-section {
  display: inline;
}

.envimage {
  max-width: 30%;
  max-height: 30%;
  float: right;
}

.box2 {
  justify-content: space-around;
  display: flex;
}

#ph {
  display: flex;

  justify-content: row;
}

#ph b {
  padding-left: 1rem;
  padding-right: 1rem;
  transform: translateY(1.5vh);
}
